"use client";
import React, { useState } from "react";
import Link from "next/link";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [errors, setErrors] = useState<{ email?: string; password?: string }>({});

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    const newErrors: typeof errors = {};

    if (!email.trim()) newErrors.email = "Email is required.";
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (email && !emailRegex.test(email)) newErrors.email = "Please enter a valid email.";
    if (!password) newErrors.password = "Password is required.";

    setErrors(newErrors);

    if (Object.keys(newErrors).length === 0) {
      console.log("Login form submitted successfully!", { email, password });
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-white px-4">
      <div className="w-full max-w-[400px] p-6 bg-white rounded-xl shadow-[0_2px_8px_rgba(0,0,0,0.1)]">
        <h1 className="text-[22px] font-bold mb-1 text-center">
          Log in to your account
        </h1>
        <p className="text-[15px] text-gray-600 mb-8 text-center">
          Welcome back! Please enter your details.
        </p>

        <form onSubmit={handleSubmit} noValidate className="space-y-5">
          <div>
            <Label htmlFor="email" className="block mb-1.5 text-[15px] font-medium">
              Email
            </Label>
            <Input
              id="email"
              type="email"
              placeholder="you@example.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full h-10 px-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
              aria-invalid={!!errors.email}
            />
            {errors.email && (
              <p className="text-red-600 text-xs mt-1">{errors.email}</p>
            )}
          </div>

          <div>
            <Label htmlFor="password" className="block mb-1.5 text-[15px] font-medium">
              Password
            </Label>
            <div className="relative">
              <Input
                id="password"
                type={showPassword ? "text" : "password"}
                placeholder="********"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full h-10 px-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                aria-invalid={!!errors.password}
              />
              <button
                type="button"
                onClick={() => setShowPassword((prev) => !prev)}
                className="absolute inset-y-0 right-0 px-3 text-sm text-gray-600 hover:text-gray-800 focus:outline-none"
                aria-label={showPassword ? "Hide password" : "Show password"}
              >
                {showPassword ? "Hide" : "Show"}
              </button>
            </div>
            {errors.password && (
              <p className="text-red-600 text-xs mt-1">{errors.password}</p>
            )}
          </div>

          <Button 
            type="submit" 
            className="w-full h-10 bg-black text-white rounded-lg hover:bg-gray-800 transition-colors mt-4"
          >
            Log in
          </Button>
        </form>

        <button
          onClick={() => window.location.href = "/"}
          className="mt-6 mx-auto flex items-center space-x-1 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-md px-3 py-1 text-sm font-medium shadow-sm transition"
          aria-label="Back to landing page"
        >
          <span className="text-lg leading-none">&larr;</span>
          <span>Back</span>
        </button>

        <p className="mt-6 text-sm text-center text-gray-600">
          Don't have an account?{" "}
          <Link href="/register" className="text-blue-600 hover:underline">
            Create one
          </Link>
        </p>

        <p className="mt-8 text-center text-xs text-gray-500">
          Â© 2025 ReadAble. All rights reserved.
        </p>
      </div>
    </div>
  );
}
